<template>
    <div>
        <p class="bg-gray-300 pt-2 px-2">
            <button :class="table.nav.btn.css" @click="loadDataTable">PDT</button>
            <button :class="table.nav.btn.css" @click="loadDataTable">PTT</button>
            
        </p>

        <!--section id="p-index-table" v-if="ui.show.index_view">
            <p :class="table.index.css" @dblclick="table.index.update=!table.index.update">
                <span v-if="!table.index.update">{{ table.index.caption }} </span>
                <span v-else>update caption <input type="text" v-model.lazy="table.index.caption" @focusout="table.index.update=false"></span>                
            </p>
            <div id="pi-table"></div>
        </section-->

        <section id="pd-index-table" v-if="ui.show.table_view">
            <p :class="table.data_index.css" @dblclick="table.data_index.update=!table.data_index.update">
                <span v-if="!table.data_index.update">{{ table.data_index.caption }} </span>
                <span v-else>update caption <input type="text" v-model.lazy="table.data_index.caption" @focusout="table.data_index.update=false"></span>                
            </p>
            <div id="pt-table"></div>
        </section>
        <!--p class="bg-red-500 text-white font-bold p-10">{{ transactions }}</p-->
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Tabulator from 'tabulator-tables';

export default {
    name: "pt-view",
    data() {
        return {
            table: {
                nav: {
                    css: "bg-blue-400 text-white p-1 rounded-lg",
                    btn: {
                        css: "bg-blue-400 text-white py-1 px-3 ml-1 rounded-t-lg"
                    }
                },
                index : {
                    caption: "Projects Index Table",
                    css: "text-sm text-gray-700 font-semibold text-left px-2 py-1 cursor-pointer",
                    update: false
                },
                data_index : {
                    caption: "Projects Data Index",
                    css: "text-sm text-gray-800 font-semibold text-left px-2 py-1 cursor-pointer",
                    update: false
                },
                location : {
                    caption: "Projects Location Index",
                    css: "text-sm text-gray-800 font-semibold text-left px-2 py-1 cursor-pointer",
                    update: false
                },
                client : {
                    caption: "Projects Clients Index",
                    css: "text-sm text-gray-800 font-semibold text-left px-2 py-1 cursor-pointer",
                    update: false
                }


            },
            ui: {
                show: {
                    index_view: true,
                    table_view: true
                }
            }

        }
    },
    mounted(){
       //this.loadProjectTable()
       this.$store.dispatch('getProjectsTransactions')
    },
    computed:{
        ...mapGetters({
        projects: 'projects',
        pdiTable: "pit", // Projects Index Table
        transactions: "transactions"
        
        })
    },
    methods:{
        async showContent(content){      
            if( content === 'index'){
                this.ui.show.index_view = true;
                this.ui.show.table_view = false;
            } else if( content === 'table'){
                this.ui.show.index_view = false;
                this.ui.show.table_view = true;
            } else if( content === 'all'){
                this.ui.show.index_view = true;
                this.ui.show.table_view = true;
            } else if( content === 'none'){
                this.ui.show.index_view = false;
                this.ui.show.table_view = false;
            }
        },
        async loadProjectTable() {
            const self = this
            
            var table = new Tabulator("#pi-table", {
                //height: 250,
                data: self.pdiTable,
                layout: "fitColumns",
                pagination: "local",
                paginationSize: 5,
                paginationElement:paginationElement,
                paginationButtonCount: 3,
                paginationSizeSelector:true,
                columns: [
                    {title:"Id", field:"id", width:150},
                    {title:"Name", field:"name", width:200},
                    {title:"Author", field:"author", width:120},
                    {title:"Created", field:"created", width:100},
                    {title:"Updated", field:"updated", width:100} 
                ],
                rowClick: function(e, row){
                    
                    self.$store.dispatch('getProject', row.getData().id)
                    self.$router.push('/project/:' + row.getData().id )
                    
                   

                }
            });
        },
        async loadDataTable() {
            const self = this
            var table = new Tabulator("#pt-table", {
                //height: 245,
                data: self.pdiTable,
                layout: "fitColumns",
                pagination: "local",
                paginationSize: 10,
                //paginationElement:paginationElement,
                paginationButtonCount: 3,
                paginationSizeSelector:true,
                columns: [
                    {title:"Id", field:"id", width:80},
                    {title:"Name", field:"name", width:150},
                    {title:"Address", field:"address", width:200},
                    {
                        title: "State",
                        columns: [
                            {title:"Active", field:"state.active"},
                            {title:"Completed", field:"state.completed"},
                            {title:"Paused", field:"state.paused"}
                        ]
                    },
                    {title:"Author", field:"author", width:120},
                    {title:"Created", field:"created", width:100},/* formatter: "datetime", formatterParams: {
                        inputFormat: "000000000",
                        outputName: "DD/MM/YY",
                        invalidPlaceholder: "(invalid date)"
                    }},*/
                    {title:"Updated", field:"updated", width:100} 
                ],
                rowClick: function(e, row){
                    
                    self.$store.dispatch('getProject', row.getData().id)
                    self.$router.push('/project/:' + row.getData().id )
                }
            });

        }
    }
    
}
</script>

