<template>
  <div class="home lg:flex sm:flex-row">
   

    <div class="card bg-gray-100 flex flex-col border border-gray-400 p-5 w-1/4 h-3/4 rounded-md overflow-y-auto">
    <p class="flex border border-green-600  mb-2 bg-white rounded-lg shadow"><span class="ml-2 mt-1 w-1/3"><Logo size="small"/>
    </span> <span class="flex bg-green-600 px-2 py-3 ml-5 w-2/3 text-lg text-white font-bold rounded-r-lg shadow relative right-0">
    <span class="ml-10">Version 2.1.0</span></span></p>

    
      
      <a href="http://localhost:5984/_utils" target="-blank"><img class="w-32 h-18 " src="@/assets/imgs/pngs/couchdb.png"/></a>
      
    </div><!-- Left User Interface -->  
    <!-- Data Display --> 
    <div class="border border-gray-400 p-5 w-3/4 ml-1 mr-10 mb-5 rounded-md overflow-y-auto">

<ul class="flex border-b">
    <li class="-mb-px mr-1">
      <a class="bg-white inline-block border-l border-t border-r rounded-t py-2 px-4 text-blue-700 cursor-pointer font-semibold" @click="showContent('card')">Card View</a>
    </li>
    <li class="mr-1">
      <a class="bg-white inline-block py-2 px-4 text-blue-500 hover:text-blue-800 cursor-pointer font-semibold"  @click="showContent('table')">Table</a>
    </li>
    <li class="mr-1">
      <a class="bg-white inline-block py-2 px-4 text-blue-500 hover:text-blue-800 cursor-pointer font-semibold"  @click="showContent('all')">All</a>
    </li>
    <li class="mr-1">
      <a class="bg-white inline-block py-2 px-4 text-gray-400 cursor-pointer font-semibold"  @click="showContent('none')">None</a>
    </li>
</ul>
    
    <div class="bg-gray-400 pt-1">
    
          <ptView v-if="ui.show.tableView"/>
          <projectsIndex v-if="ui.show.cardView"></projectsIndex>
    </div>
    
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import {mapGetters} from 'vuex'
import Logo from '@/components/ui/logo.vue'
import ptView from '@/components/project/projectTableView.vue'
import projectsIndex from '@/components/project/projectsIndex.vue'




export default {
  name: 'Home',
  data(){
    return {
      ui: {
        show: {
          cardView: false,
          tableView: true
        }
      }
    }
  },

  components: {  
    Logo,
    projectsIndex,
    ptView
    
  },
  mounted() {
    this.$store.dispatch('getProjectsTable')
  },
  
  computed:{
    ...mapGetters({
      
      count: 'projectscount',
      conflicts: 'conflicts',
      conflictscount: 'conflictscount'
    })
  },
  methods: {
    async showContent(content){
      
      if( content === 'card'){
        this.ui.show.cardView = true;
        this.ui.show.tableView = false;
      } else if( content === 'table'){
          this.ui.show.cardView = false;
          this.ui.show.tableView = true;
      } else if( content === 'all'){
          this.ui.show.cardView = true;
          this.ui.show.tableView = true;
      } else if( content === 'none'){
        this.ui.show.cardView = false;
        this.ui.show.tableView = false;
    }
    }
  }
}
</script>
